@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
* {
  background-color: rgb(160,173,180);
  /* margin: 1px; */
  font-family: 'Indie Flower', cursive;
}

.bio {
  padding: 75px;
  font-size: 22px;
  color: #552A92;
}

a.button {
  -webkit-appearance: button;
  appearance: button;

  text-decoration: none;
  color: #000;
  color: initial;
}




.image {
  height: 5%;
  width: 20%;
  position:relative;
  border: 1px solid black;
  padding: 1px;
  border-radius: 200%;
}

h2 {
  text-align: center;
  border-top: black;
  color: #552A92;
  padding-top: 20px;
  font-weight: bold;
}


p {
    font-size: 25px;
}

.email {
    font-size: 25px;
}
 h1 {
    color:#552A92;
    font-size: x-large;
    word-spacing: 2px;
    margin:30px;
    padding-top: 15px;
} 


ul#menu li a:hover{
    color: #552A92;
    text-decoration: none;
} 
a {
    color:#552A92;
    font-size: 20px;
    font-weight: bold;
}

.nav-link {
    color: #552A92;
}


